<template>
  <div>
    <stock-navigation-bar></stock-navigation-bar>

    <div class="table-title">  {{this.getProduct.rows.length}} ,    {{this.getProduct.rows.length - this.getProductProgressing.rows.length}} ,    {{this.getProductProgressing.rows.length}}</div>
    <div class="subcontents-define">  {{this.getProduct.rows.length}} 
      <div class="add-button-container"><button class="crud-button" @click="showModal = 1">  </button></div>
    </div>
    <vue-good-table
      :columns="getProduct.columns"
      :rows="getProduct.rows"
      :sort-options="{
        enabled: true,
        initialSortBy: {field: 'Code', type: 'asc'}
      }"
      :select-options="{
        enabled: true,
        clearSelectionText: ' '
        }"
      :search-options="{ enabled: true }"
      :pagination-options="{
        enabled: true,
        mode: 'pages',
        perPage: 10,
        perPageDropdown: [5, 10, 15, 20]
      }"
      @on-row-click="nop"
      @on-selected-rows-change="selectionChanged">
      <div slot="selected-row-actions">
        <button class="crud-button in-table-button" @click="showModal = 2" v-bind:class="{ inactive : getSelectedPost.length != 1 }">  </button>
        <button class="crud-button in-table-button" @click="deleteRequest(getSelectedPostID)">  </button>
      </div>
    </vue-good-table>

    <modal v-if="showModal" @close="showModal = 0">
      <div slot="header">
        <div v-if="showModal == 1"> </div>
        <div v-if="showModal == 2"> </div>
      </div>
      <div slot="body">
        <p>
           :
          <select v-model="requestBody.data.Specification">
            <option value="T"></option>
            <option value="B"></option>
            <option value="O"></option>
            <option value="G"></option>
            <option value="S"></option>
            <option value="E"></option>
          </select>

           :
          <select v-model="requestBody.data.codeSex">
            <option value="M"></option>
            <option value="F"></option>
          </select>
          </p>
          <p>
           :
          <select v-model="requestBody.data.codeState">
            <option value="S">S</option>
            <option value="A">A</option>
            <option value="B">B</option>
          </select>

           :
          <select v-model="requestBody.data.Progress">
            <option value="1"> </option>
            <option value="2"> </option>
            <option value="3"> </option>
            <option value="4"></option>
            <option value="9"></option>
          </select>
        </p>
        <p> : <input class="title-input" type="textarea" maxlength="100" v-model="requestBody.data.Info"/></p>
        <p> : <input class="title-input" type="number" v-model="requestBody.data.FirstPrice"/></p>
        <p> : <input class="title-input" type="number" v-model="requestBody.data.DiscountRate"/></p>
        
      </div>
      <div slot="footer">
        <div>
          <button v-if="showModal == 1" @click="createRequest(requestBody)" v-on:click="showModal = 0"></button>
          <button v-if="showModal == 2" @click="updateRequest(requestBody)" v-on:click="showModal = 0"></button>
          <button v-on:click="showModal = false"></button>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import { mapActions,mapMutations,mapGetters } from 'vuex'

import { VueGoodTable } from 'vue-good-table'
import 'vue-good-table/dist/vue-good-table.css'

import StockNavigationBar from '@/views/Stock/StockNavigationBar'

import Modal from './Modal'

export default {
  created() {
    this.getRequest('Product')
    this.setSelectedPost([])
    this.clickMenuButtonIcon(3)
  },
  data() {
    return {
      showModal: false,
      requestBody: {
        requestName: "Product",
        data: {
            Specification: "",
            codeState: "",
            codeSex: "",
            Info: "",
            FirstPrice: 0,
            DiscountRate: 0,
            Progress: "",
        }
      },
    }
  },
  methods: {
    ...mapActions(['getRequest']),
    ...mapActions(['deleteRequest']),
    ...mapMutations(['setSelectedPost']),
    ...mapMutations(['clickMenuButtonIcon']),
    selectionChanged(params) {
      // params.row - row object 
      // params.pageIndex - index of this row on the current page.
      // params.selected - if selection is enabled this argument 
      // indicates selected or not
      // params.event - click event

      // params.selectedRows - all rows that are selected (this page)
      this.setSelectedPost(params.selectedRows)
    },
    ...mapActions(['createRequest']),
    ...mapActions(['updateRequest']),
    nop() {}
  },
  computed: {
    ...mapGetters(['getProduct']),
    ...mapGetters(['getProductProgressing']),
    ...mapGetters(['getSelectedPost']),
    ...mapGetters(['getSelectedPostID']),
  },
  components: {
    VueGoodTable,
    Modal,
    StockNavigationBar
  }
}
</script>

